/* About.css */

/* General styles */
body {
    margin: 0;
    font-size: 16px;
}

/* Base styling */
.about {
    background-color: white;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    min-width: 320px; /* Set a minimum width for small screens */
}

.about .container {
    width: 100%;
    max-width: 1240px;
    margin: auto;
    display: grid;
    grid-gap: 1rem;
}

.about h2 {
    font-size: 3rem;
    padding-bottom: 3%;
    text-align: center;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color:2B033D;
}

.about h3 {
    text-align: center;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: large;
    padding: 5%;
    color: white;
    width: 90%;
}

.about p{
    text-align: center;
    font-family: 'TheBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: 1.4em;
    line-height: 140%;
    padding: 3%;
    color: #2B033D; 
    min-width: 80%;
}

.p4{
    font-size: 3rem;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    padding: 5%;
    color: white;
    width: 90%;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
    .about {
        padding: 4rem 1rem 0 1rem;
    }

    .about h2 {
        font-size: 2rem;
        margin-left: 5%;
    }

    .about p {
        padding: 10%;
        size: 10%; 
        text-align: justify;
    }

    .image-row {
        padding: 3%;
    }

    /* New/refined styles for responsive image layout: */
    .image-container {
        grid-template-columns: repeat(2, 1fr); /* Set 2 columns directly using grid */
        grid-gap: 1rem;
        padding-bottom: 7%; /* Adjust padding as needed */
    }

    .image-row {
        display: flex;
        flex-wrap: wrap; /* Ensure images wrap on smaller screens */
        justify-content: space-between;
        padding: 10%; /* Remove unnecessary padding */
    }

    .square {
        min-width: 100%; /* Allow full width for responsive layout */
        margin: 1em auto; /* Center each image within its column */
    }

    .square img {
        width: 65%; /* Adjust image width to fit two per row */
        height: auto;
    }
}

/* Images styling with adjusted padding */
.image-container {
    display: grid;
    grid-gap: 1rem;
    padding-bottom: 5%; /* Adjust the value as needed */
}

.image-row {
    display: flex;
    flex-wrap: wrap; /* Ensure images wrap on smaller screens */
    justify-content: space-between;
    padding: 3%;
}

.square {
    position: relative;
    justify-content: center;
    display: flex;
    align-items: center;
    width: 50%;
}

.square img {
    min-width: 40%;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #2B033D;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffffff;
    transition: opacity 0.3s ease-in-out;
}

.square:hover .overlay {
    opacity: 0.9;
}

.overlay-title {
    font-size: 1.5rem;
}
